.homeContainer {
  padding-top: 50px;
}

.homeContainer .listContainer button {
  font-size: 20px;
}

.homeContainer .listContainer h2 {
  width: 100%;
  border-bottom: 1px #b2b2b2 solid;
}

.homeContainer .listContainer ul, .homeContainer .listContainer li {
  display: block;
  padding: 0;
  margin: 0;
}

.homeContainer .listContainer ul {
  min-height: 400px;
}

.homeContainer .listContainer ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px 0px;
  background: #bcdff5;
  border-bottom: #b6b6e2 1px solid;
  -webkit-box-shadow: 0px 5px 13px rgba(61, 72, 131, 0.21);
          box-shadow: 0px 5px 13px rgba(61, 72, 131, 0.21);
}

.homeContainer .listContainer ul li .textDesc {
  width: 100%;
  text-align: center;
  padding-top: 7px;
}

